import React from 'react'
import './index.less'
import {Link, Redirect, Route} from 'react-router-dom'
import WithdrawAdmin from "./WithdrawAdmin";
import WorkCheck from "./WorkCheck";
import UserCheck from "./UserCheck";
import {observer} from "mobx-react";
import store from './store'


@observer
class AdminPC extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
        store.fetchData();
    }

    componentDidMount() {
    }
    render() {
        return (
            <div className='admin-pc'>
                <div className='nav'>
                    <div className='nav-route'>
                        <Link to={`${this.props.match.url}/with_draw`}>
                            提现审核
                        </Link>
                        <Link to={`${this.props.match.url}/user_check`}>
                            用户信息
                        </Link>
                        <Link to={`${this.props.match.url}/work_check`}>
                            任务排查
                        </Link>
                    </div>
                    <div className='routes-container'>
                        <Route exact path={this.props.match.url} render={() => (
                            <Redirect to={`${this.props.match.url}/with_draw`}/>
                        )}/>
                        {/*提现审核页面*/}
                        <Route path='/adminPC/with_draw' exact component={WithdrawAdmin}/>
                        {/*任务排查页面*/}
                        <Route path='/adminPC/work_check' exact component={WorkCheck} store={store}/>
                        {/*用户基础信息页面*/}
                        <Route path='/adminPC/user_check' exact component={UserCheck}/>
                    </div>
                </div>
            </div>
        )
    }
}


export default AdminPC;