import { Form, Icon, Input, Button, Checkbox } from 'antd';
import React from "react";
import axios from "axios";
import {withRouter} from "react-router-dom";
const baseUrl = 'http://120.79.229.14:8888';

class NormalLoginForm extends React.Component {
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                console.log('deskId ', this.props.match.params.deskId);

                axios.post(
                    baseUrl + '/login',
                    null,
                    {
                        params:
                            {
                                studentName: values.studentName,
                                studentPassword: values.studentPassword,
                                tableId: this.props.match.params.deskId,
                            }
                    })
                    .then((r) => {
                        if (r.data.code===100){
                            alert('成功坐下');
                            setTimeout(() => {
                                // 刷新缓存
                                document.location.reload()
                            }, 800)
                        }else {
                            alert(r.data.msg);

                        }
                    })
            }
        });
    };

    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form">
                <Form.Item>
                    {getFieldDecorator('studentName', {
                        rules: [{ required: true, message: 'Please input your username!' }],
                    })(
                        <Input
                            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            placeholder="姓名"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('studentPassword', {
                        rules: [{ required: true, message: 'Please input your Password!' }],
                    })(
                        <Input
                            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            placeholder="密码"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        坐下
                    </Button>
                </Form.Item>
            </Form>
        );
    }
}

const LoginForm= Form.create({ name: 'normal_login' })(NormalLoginForm);
export const Login = withRouter(LoginForm)
