import React from 'react';
import {observer} from 'mobx-react';
import {Link, Redirect, Route, withRouter} from "react-router-dom";
import {Avatar, Button, Card, Icon, message, Modal} from "antd";
import {baseUrl} from "../../../config";
import Zmage from "react-zmage";
import moment from 'moment'
import store from '../store'
import axios from "axios";
import './index.less'
import {WorkerCmmUpload} from "../WorkerCmmIndex/WorkerCmmUpload";

@observer
class WorkPass extends React.Component {
    constructor(props) {
        super(props);
        console.log(this.props);
        this.state = {
            status:this.props.status,//待审核1/已审核0/未通过-1
            visibleModal:false,
            contentModal:'',
        }
    }
    pass = (id_worker) => {
        axios.get(
            baseUrl + '/api/set_worker_status',
            {
                params: {
                    token: localStorage.customer_token,
                    sure: 0,    //['已审核', '已提交', '未提交', 5 => '已领取', -1 => '未通过', -2 => '客服中']
                    id_work: this.props.id_work,
                    id_worker: id_worker
                }
            })
            .then((Response) => {
                const d = Response.data;
                console.log('返回数据', Response.data);
                if (d.sorry !== undefined || d.error !== undefined) {
                    message.error(d.sorry || d.error);
                } else {
                    // 刷新缓存
                    message.success('已审核通过！');
                    // this.forceUpdate();
                    document.location.reload();
                }
            })
    };
    passNot = (id_worker) => {
        axios.get(
            baseUrl + '/api/set_worker_status',
            {
                params: {
                    token: localStorage.customer_token,
                    sure: -1,    //['已审核', '已提交', '未提交', 5 => '已领取', -1 => '未通过', -2 => '客服中']
                    id_work: this.props.id_work,
                    id_worker: id_worker
                }
            })
            .then((Response) => {
                const d = Response.data;
                console.log('返回数据', Response.data);
                if (d.sorry !== undefined || d.error !== undefined) {
                    message.error(d.sorry || d.error);
                } else {
                    // 刷新缓存
                    message.success('操作成功');
                    // this.forceUpdate();
                    document.location.reload();
                }
            })
    };
    renderWorkersCmm = (id)=>{
        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    token: localStorage.customer_token,
                    method: 'senter_get',
                    id_work: this.props.id_work,
                    id_communication: id
                }
            })
            .then((Response) => {
                this.setState({
                    oneWorkerCmm: Response.data.communications[0],
                });
                // 设置缓存
            });

    }
    showCommunications = (id) => {
        this.renderWorkersCmm(id);
        const p = this.state.oneWorkerCmm;
        console.log(p)
        this.setState({
            visibleModal: true,
            contentModal: <div>
                <h2 className='phone'>联系方式:{p.phone}</h2>
                <h2 className='phone'>(添加时请备注：【小蓝兼职】)</h2>
                <img src={`${baseUrl}/${p.wx_png}`} alt=''
                     className='avatar'
                     width='100%'
                />
                <h5>提示：长按识别二维码添加微信</h5>
            </div>,
        });
    };
    handleOk = () => {
        this.setState({
            visibleModal: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleModal: false,
        });
    };
    render() {
        let count = 0;
        store.workers.map(w=> {
            const checkStatus = w.status === this.state.status;
            if (checkStatus) {
                count += 1;
            }
        });
        return (
            <div className="index-workers">
                人数:{count}
                {store.workers.map(w=>{
                    const checkStatus = w.status === this.state.status;
                    if (checkStatus){
                    return (<div className='workers-cell'>
                    <div className='avatar-name'>
                        <Avatar size={40} shape='square'
                                src={`${baseUrl}/${w.worker_avatar_url}`}
                                className='avatar'>头像</Avatar>
                        <span className='name'>{w.worker_name}</span>
                    </div>
                        <div className='workers-files'>
                        {w.datas.map(m => {
                            return (
                                <img src={`${baseUrl}/${m.content}`} alt=''
                            width='30%'
                            onClick={()=>{
                                const WeixinJSBridge = window.WeixinJSBridge;
                                WeixinJSBridge.invoke("imagePreview",{
                                    "urls":[`${baseUrl}/${m.content}`],
                                    "current":`${baseUrl}/${m.content}`
                                });
                            }
                        }
                            />
                            )
                        })}
                        </div>
                        <div className='accept-time'>接单时间:{moment.unix(w.created_at).format("MM-DD HH:mm")}</div>
                        {
                            this.state.status===1
                                ?<div>
                                <Button type='primary' onClick={this.pass.bind(this, w.id)}>审核通过</Button>
                                <Button type='danger' onClick={this.passNot.bind(this, w.id)}>审核不通过</Button>
                                </div>
                                :null
                        }
                        <br/>
                        {
                            parseInt(w.id_communication)!==0
                                ?
                        <Button
                            onClick={this.showCommunications.bind(this,w.id_communication)}
                        >
                            联系他
                        </Button>
                                :null
                        }
                        <Modal
                            visible={this.state.visibleModal}
                            onOk={this.handleOk}
                            onCancel={this.handleCancel}
                            style={{textAlign: 'center', top: 20}}

                        >
                            {this.state.contentModal}
                        </Modal>
                </div>)
                    }
                }
                )}
            </div>
        );
    }
}


export default withRouter(WorkPass);