import React from 'react'
import './index.less'
import {Redirect, Route, Switch} from 'react-router-dom'
import Theme from './pages/Theme/index';
import Face from './pages/Face/index';
import About from './pages/About';
import NavFooter from './cpns/NavFooter/index';
import SellerIndex from './pages/Seller/index';
import Admin from './pages/Seller/admin/index';
import moment from 'moment'
import {baseUrl} from "./config";
import {Icon, notification} from "antd";
import IndexPalace from "./pages/Palace/Index";
import Work from "./cpns/Work";
import Lab from "./pages/Lab";
import store from "./pages/Lab/store";
import storeWork from "./pages/Work/store";
import IndexWork from "./pages/Work";
import Pay from "./cpns/Pay";
import AdminPC from "./pages/adminPC";
import EnsureLoggedInContainer from "./cpns/EnsureLoggedInContainer";
import IndexSkill from "./pages/Skills/Index";

export default class App extends React.Component {

    render() {
        return (
            <div>
                <div className='main'>
                    <Switch>
                        <Route path="/adminPC" component={AdminPC}/>
                        <EnsureLoggedInContainer>
                            {/*<Route path="/" exact component={IndexPalace}/>*/}
                            <Route exact path="/" component={() => <Redirect to="/palace" />}/>
                            <Route path="/seller" component={SellerIndex}/>
                            <Route path="/admin" component={Admin}/>
                            <Route path="/theme" component={Theme}/>
                            {/*<Route path="/w/:id" component={Work}/>*/}
                            <Route path="/work/:id" render={(props) => <IndexWork {...props} store={storeWork}/>}/>
                            <Route  path="/w/:id"
                                   render={(props) => <IndexWork {...props} store={storeWork} visitor={true}/>}/>
                            <Route path="/face/:id" component={Face}/>
                            <Route path="/about" component={About}/>
                            <Route path="/lab" render={(props) => <Lab {...props} store={store}/>}/>
                            <Route path="/pay" component={Pay}/>
                            <Route path="/palace" component={IndexPalace}/>
                            <Route path="/skill" component={IndexSkill}/>
                            {/*<Redirect from="/" to='/palace'/>*/}
                        </EnsureLoggedInContainer>
                    </Switch>
                </div>
                <NavFooter/>
            </div>
        )
    }
}