import React from 'react'
import './index.less'
import axios from 'axios'
// import {baseUrl} from "../../../config";
import {Avatar, Divider, message, Table, Tag} from 'antd';
import * as moment from "moment";
import {observer} from "mobx-react";
import store from '../store'

const baseUrl = 'https://www.newbrandser.com';
const {Column, ColumnGroup} = Table;

@observer
class WithdrawAdmin extends React.Component {
    render() {
        return (
            <div>
                <Table dataSource={store.users}>
                    <Column title="用户id" dataIndex="id" key="id"/>
                    <Column
                        title="头像"
                        dataIndex="avatarUrl"
                        key="avatarUrl"
                        render={avatarUrl => (
                            <span>
                                <Avatar size={48} src={`${baseUrl}/${avatarUrl}`}/>
                            </span>
                        )}
                    />
                    <Column title="昵称" dataIndex="nickName" key="nickName"/>
                    <Column title="硬币总额" dataIndex="money" key="money"/>
                    <Column title="提现中硬币" dataIndex="money_locked" key="money_locked"/>
                    <Column title="累计获得硬币" dataIndex="money_had" key="money_had"/>
                    <Column title="累计提现硬币" dataIndex="money_give" key="money_give"/>
                    <Column
                        title="生成时间"
                        dataIndex="created_at"
                        key="created_at"
                        render={created_at => (
                            <span>
                                {moment.unix(created_at).format("YYYY-MM-DD HH:mm")}
                            </span>
                        )}
                    />
                    <Column
                        title="更新时间"
                        dataIndex="updated_at"
                        key="updated_at"
                        render={updated_at => (
                            <span>
                                {moment.unix(updated_at).format("YYYY-MM-DD HH:mm")}
                            </span>
                        )}
                    />
                </Table>
            </div>
        )
    }

}

export default WithdrawAdmin;
