import React from 'react'
import './index.less'
import axios from 'axios'
// import {baseUrl} from "../../../config";
import {Divider, message, Table, Tag} from 'antd';
import * as moment from "moment";
import {observer} from "mobx-react";
import store from '../store'

const baseUrl = 'https://www.newbrandser.com';
const {Column, ColumnGroup} = Table;

@observer
class WithdrawAdmin extends React.Component {
    actionWithDraw = (ok,id_inf) =>{
        console.log(id_inf);
        axios.get(
            baseUrl + '/api/ti_xian_ok',
            {
                params: {
                    super_token: '24_gUI7EwZGw-npGJDZHX0wEPkASQW1Aa9PAn4O0gmsc-paJaZ_iCWtHZDZu5WBiPOW55-KjzynoxOoCNmsGQNdmA',
                    id_inf:id_inf,
                }
            })
            .then((Response) => {
                console.log(Response.data);
                const d = Response.data;
                if (d.message==='success') {
                    message.success('操作成功');
                    this.forceUpdate();
                }else {
                    message.error(d.error||d.sorry);
                }

            })
    };
    render() {
        return (
            <div>
                <Table dataSource={store.withDraw}>
                    <Column title="提现id" dataIndex="id" key="id"/>
                    <Column title="用户id" dataIndex="id_inf" key="id_inf"/>
                    <Column
                        title="生成时间"
                        dataIndex="created_at"
                        key="created_at"
                        render={created_at => (
                            <span>
                                {moment.unix(created_at).format("YYYY-MM-DD HH:mm")}
                            </span>
                        )}
                    />
                    <Column
                        title="更新时间"
                        dataIndex="updated_at"
                        key="updated_at"
                        render={updated_at => (
                            <span>
                                {moment.unix(updated_at).format("YYYY-MM-DD HH:mm")}
                            </span>
                        )}
                    />
                    <Column title="本次提现积分" dataIndex="content" key="content"/>
                    <Column
                        title="应付多少钱"
                        dataIndex="content"
                        key="content"
                        render={content => (
                            <span>
                                {content*0.01*0.97}
                            </span>
                        )}
                    />
                    <Column
                        title="状态"
                        dataIndex="status"
                        key="status"
                        render={status => (
                            <span>
                                {status===0?'未提现':'已提现'}
                            </span>
                        )}
                    />
                    <Column
                        title="操作"
                        key="action"
                        render={(text, record) => (
                            <span>
                              <span onClick={this.actionWithDraw.bind(this,1,record.id)}>通过</span>
                              <Divider type="vertical"/>
                              <span onClick={this.actionWithDraw.bind(this,0,record.id)}>不通过</span>
                            </span>
                        )}
                    />
                </Table>
            </div>
        )
    }

}

export default WithdrawAdmin;
