import {action, computed, configure, observable} from 'mobx';
import axios from 'axios'
// import {baseUrl} from "../../../../../config";

const baseUrl = 'https://www.newbrandser.com';

configure({
    enforceActions: 'observed'
});

class StoreAdmin {
    @observable originData = [];

    @computed get withDraw() {
        return this.originData.Money_will_give;

    };

    @computed get users() {
        return this.originData.Users;

    };

    // 动作
    @action setData = (v) => {
        this.originData = v;
    };

    @action
    async fetchData() {
        axios.get(
            baseUrl + '/api/ti_xian_backend',
            {
                params: {
                    super_token: '24_gUI7EwZGw-npGJDZHX0wEPkASQW1Aa9PAn4O0gmsc-paJaZ_iCWtHZDZu5WBiPOW55-KjzynoxOoCNmsGQNdmA',
                }
            })
            .then((Response) => {
                this.setData(Response.data);
            });
    };

}

export default new StoreAdmin();