import React from 'react'
import {baseUrl} from "../config";
import {Icon, notification} from "antd";
import AdminPC from "../pages/adminPC";
import moment from "moment";

class EnsureLoggedInContainer extends React.Component {

    componentDidMount() {
        console.log('HOC.props', this.props);
        // 进入登陆
        if (document.location.host === "localhost:3000") {
            console.log('开发者模式');
            localStorage.customer_token = '25_dgzKu3fxzszctxegVJ9jhcL9HkhXtis77GGA4SdsRCX-wMPS9v5Pe9BAQPLRm6bb5bIt5v8LWAk5GP6b700EyA'
        } else {
            console.log('生产模式');
            // 没有时间戳 -> 未登陆 ->跳转
            // 有时间戳 ->当前小于时间戳时间 -> 设置token
            // 有时间戳 ->当前大于时间戳时间 -> 跳转
            const tt = localStorage.token_time;
            const aj = localStorage.afterJump;
            const jump = () => {
                const u = encodeURIComponent(baseUrl);
                document.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx5e44595fadf1d2a2&redirect_uri=${u}%2fwechat_redirect&response_type=code&scope=snsapi_userinfo&state=index.html`
            };
            const cookie2dic = () => {
                var cookies = document.cookie.split("; ");
                var dic = [];
                for (let i = 0; i < cookies.length; i++) {
                    const e = cookies[i];
                    var key_shit = e.split("=")[0];
                    var val = unescape(e.split("=")[1]);
                    if (key_shit === 'nickName') {
                        val = decodeURI(e.split("=")[1])
                    }
                    dic[key_shit] = val
                }
                console.log('解析后的cookie:', dic);
                return dic
            };
            // 未登录过
            if (tt === undefined && aj === undefined) {
                console.log('第一次登陆');
                localStorage.afterJump = true;
                jump()
            }
            // token超时
            if (tt !== undefined || localStorage.customer_token === 'undefined') {
                try {
                    if (JSON.parse(tt) < moment().unix()) {
                        notification.open({
                            message: '正在自动登陆',
                            placement: 'bottomLeft',
                            icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                        });
                        localStorage.token_time = moment().add(2, 'days').unix();
                        localStorage.afterJump = true;
                        jump()
                    }
                } catch (err) {
                    try {
                        console.log('删除掉localstorage');
                        localStorage.removeItem('token_time');
                        localStorage.removeItem('afterJump');
                        localStorage.removeItem('customer_token');
                        notification.open({
                            message: '身份认证超时，正在刷新',
                            placement: 'bottomLeft',
                            icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                        });
                        jump()
                    } catch (e) {
                        console.log(e)
                    }
                    console.log(err)
                }
            }
            // 已跳转
            if (aj) {
                console.log('已跳转');
                const dt = cookie2dic();
                localStorage.customer_token = dt.token;
                localStorage.token_time = dt.token_time
            }
        }
    }

    render() {
        return this.props.children
    }
}
export default EnsureLoggedInContainer;