import React from 'react'
import {Link, Redirect, Route} from 'react-router-dom'
import './index.less'
import SortByPost from "./SortByPost";
import SortByDone from "./SortByDone";
import Frap from "../../../cpns/Frap";
import {Radio} from "antd";
import axios from "axios";
import {baseUrl} from "../../../config";
import wx from "weixin-js-sdk";

export default class IndexPalace extends React.Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    componentDidMount() {
        document.title = "小蓝兼职，发现更多好兼职～";
        this.share()

    }
    share = () => {
        axios.get(baseUrl + '/api/get_jssdk',
            {
                params: {
                    url: window.location.href.split('#')[0]
                }
            }).then(res => {
            const params = {
                debug: false,
                appId: res.data.appId,
                timestamp: res.data.timestamp,
                nonceStr: res.data.nonceStr,
                signature: res.data.signature,
                jsApiList: [
                    'onMenuShareTimeline',
                    'onMenuShareAppMessage'
                ]
            };
            wx.config(params);
            wx.ready(function () {
                // config信息验证成功后会执行ready方法
                //通过ready接口处理成功验证
                wx.onMenuShareAppMessage({
                    // 分享给朋友
                    title: '小蓝兼职，发现更多好兼职～', // 分享标题
                    desc: '找兼职，就上小蓝兼职，100＋合作商家，靠谱！',
                    link: res.data.str, // 分享链接
                    imgUrl:
                        `${baseUrl}/storage/9XH9nlr8bEmonCViyLAV0qBf3HHtogDNKSZqMyT9.png`, // 分享图标
                    type: "", // 分享类型,music、video或link，不填默认为link
                    dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
                    success: function (res) {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });
                wx.onMenuShareTimeline({
                    //分享朋友圈
                    title: '小蓝兼职，发现更多好兼职～', // 分享标题
                    desc: '找兼职，就上小蓝兼职，100＋合作商家，靠谱！',
                    link: res.data.str, // 分享链接
                    imgUrl: `${baseUrl}/storage/9XH9nlr8bEmonCViyLAV0qBf3HHtogDNKSZqMyT9.png`, // 分享图标
                    success: function (res) {
                        // 用户确认分享后执行的回调函数
                    },
                    cancel: function () {
                        // 用户取消分享后执行的回调函数
                    }
                });

            });
        });
    };
    handleChange = (vl) => {
        const v = vl.target.value;
        console.log(vl);
        console.log(v);
        this.setState({
            tpl: v === 'all' ? undefined : v,
        });
    };

    render() {
        return (
            <div className="Palace">
                <div className='palace-header'>
                    <div className='theme-header'>
                        {/*背景*/}
                    </div>
                    <div className='header-text'>
                        任务广场
                    </div>
                </div>
                {/* 子路由导航 */}
                <div className='sticky'>

                    <div className='nav-route'>
                        <Link
                            to={`${this.props.match.url}/SortByPost`}
                            className={document.location.hash.includes('SortByPost') ? 'active' : 'unactive'}
                        >
                            最近发布
                        </Link>
                        <Link
                            to={`${this.props.match.url}/SortByDone`}
                            className={document.location.hash.includes('SortByDone') ? 'active' : 'unactive'}
                        >
                            最近截止
                        </Link>
                    </div>
                    <div className='tpl-select'>
                        <Radio.Group defaultValue='all' onChange={this.handleChange}>
                            <Radio.Button value='all'>全部</Radio.Button>
                            <Radio.Button value="1">代办类</Radio.Button>
                            <Radio.Button value="4">兼职类</Radio.Button>
                            <Radio.Button value="2">转发类</Radio.Button>
                            <Radio.Button value="3">问卷类</Radio.Button>
                            <Radio.Button value="0">其他</Radio.Button>
                        </Radio.Group>
                    </div>
                </div>

                {/* 子路由 */}
                <div className='routes-container'>
                    <Route exact path={this.props.match.url} render={() => (
                        <Redirect to={`${this.props.match.url}/SortByPost`}/>
                    )}/>
                    <Route
                        exact
                        path={`${this.props.match.url}/SortByPost`}
                        component={() => <SortByPost tpl={this.state.tpl}/>}
                    />
                    <Route
                        exact
                        path={`${this.props.match.url}/SortByDone`}
                        component={() => <SortByDone tpl={this.state.tpl}/>}
                    />
                </div>
                <Frap/>
            </div>

        )
    }
}