import React from 'react'
import {Avatar, Button, Card, Icon, message, Spin} from 'antd'
import './index.less'
import axios from "axios";
import {baseUrl} from "../../config";
import moment from "moment";
import {Link} from "react-router-dom";
import Zmage from 'react-zmage'

export default class WorkCell extends React.Component {
    constructor(props) {
        super(props);
        console.log(this.props)
    }

    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 4:
                return "兼职类";
            case 0:
                return "其他类";
            default:
                return "其他类";
        }
    };
    jumpToWork = (id) => {
        window.location.hash = `#/w/${id}`
    };

    render() {
        const p = this.props.workInfo;
        return (
            <div
                className=
                    {
                        p.stop_at < moment().unix()
                            ? 'work-list-cell-index cell-disable'
                            : 'work-list-cell-index'
                    }
                id={p.id}
                onClick={this.jumpToWork.bind(this, p.id)}
            >
                {/*<Link to={`/w/${p.id}`}>*/}
                <div className='work-cell-row'>
                    {/*标题*/}
                    <span className='work-name'>{p.work_name}</span>
                    {/*报酬*/}
                    <div className='row-1-right'>
                        <span
                            className='work_money'
                        >
                            {p.work_tpl === 1 || p.work_tpl === 4|| p.work_tpl === 0 ? `${p.work_money}元` : `${p.work_money / 100}元(硬币)`}

                            </span>
                        {/*<span className='work-person-number'>*/}
                        {/*    {p.work_person_number}人*/}
                        {/*    <br/>*/}
                        {/*    需要人数*/}
                        {/*</span>*/}
                    </div>
                </div>
                {/*<div className='work-cell-row'>*/}
                {/*    <span> </span>*/}
                {/*    <span>需要:{p.work_person_number}人</span>*/}
                {/*</div>*/}

                <div className='work-cell-row'>
                    {/*/!*头像*!/*/}
                    {/*<Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}*/}
                    {/*        className='avatar'>头像</Avatar>*/}
                    {/*/!*名字*!/*/}
                    {/*<span className='name'>{p.senter_name}</span>*/}
                    {p.stop_at ?
                        <span>截止:{
                            moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                '今天内'
                                : moment.unix(p.stop_at).format("MM-DD HH:mm")
                        }

                                </span>
                        : null}
                    <span className='work-person-number'>
                            需要:{p.work_person_number}人
                        </span>
                </div>
                <div className='work-cell-row'>
                    {/*分类*/}
                    <span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>
                    {/*报名按钮*/}
                    {
                        this.props.isSenter
                            ? <Link to={`/w/${p.id}`}>查看详情</Link>
                            :
                            //过期判定
                            p.stop_at < moment().unix()
                                ?
                                <span className='failed '>
                                    已过期，查看详情
                                </span>
                                :
                                <div>
                                    {/*<Button type='primary' onClick={this.working.bind(this, p.id)}>接单</Button>*/}
                                    <span className='button-more'>
                                    {/*查看详情*/}
                                        {p.enrolled ? '已接单,查看详情' : (p.work_tpl === 2 || p.work_tpl === 3) ? (p.worker_num <= 0 ? '已满人，查看详情' : '查看详情') : '查看详情'}
                                    </span>
                                </div>

                    }
                </div>
                {/*</Link>*/}
            </div>
        )
    }
}