import React, { Component } from "react";
import axios from "axios";
import {Login} from "./Login";
import {Logout} from "./Logout";
const baseUrl = 'http://120.79.229.14:8888';

export default class IndexSit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            is_sit : false,
        };
    }

    componentDidMount() {
        //查桌子有无坐下
        this.haveBeenSited()

    }
    haveBeenSited = ()=>{
        axios.get(
            baseUrl + '/table/' + this.props.match.params.deskId,
            {
                params: {
                    // token: localStorage.customer_token,
                }
            })
            .then((r) => {
                if (r.data.code ===200){
                    this.setState({
                        is_sit:true,
                    })
                }
            })
            .catch((err) => {
                console.log(err);
            })
    };
    render() {
        return (
            <div>
                <h1>桌子号：{this.props.match.params.deskId}</h1>
                {
                    this.state.is_sit
                        ? <Logout/>
                        :<Login/>
                }
            </div>
        )
    }
}