import React from 'react';
import {observer} from 'mobx-react';
import {Link, Redirect, Route, withRouter} from "react-router-dom";
import {Avatar, Button, Card, Icon, message, Modal, notification, Radio} from "antd";
import axios from "axios";
import './index.less'
import {baseUrl} from "../../../config";
import {WorkerCmmUpload} from "./WorkerCmmUpload";

@observer
class WorkerCmmIndex extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visibleModal: false,
            communications: [],
            activeId: 0,
        }
    }

    componentDidMount() {
        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    method: 'get',
                }
            })
            .then((r) => {
                this.setState({
                    communications: r.data.communications,
                })
            })
    }

    update_worker_communication = () => {
        axios.get(
            baseUrl + '/api/update_worker_communication',
            {
                params: {
                    token: localStorage.customer_token,
                    id_worker: this.props.id_worker,
                    id_communication: this.state.activeId,
                }
            })
            .then((r) => {
                if (r.data.success !== undefined) {
                    message.success('上传成功!');
                    notification.open({
                        message: '上传成功',
                        description: '正在刷新...',
                        placement: 'bottomLeft',
                        icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                    });
                    setTimeout(() => {
                        document.location.reload()
                    }, 1000);

                } else {
                    message.error('上传失败，请稍后重试!');

                }
            });

    };
    showCommunicationsUpload = () => {
        this.setState({
            visibleModal: true,
            contentModal: <WorkerCmmUpload/>,
        });
    };
    handleOk = () => {
        this.setState({
            visibleModal: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleModal: false,
        });
    };
    clickWhich = (id) => {
        this.setState({
            activeId: id,
        })
    };

    render() {
        return (
            <div className='WorkerCmmIndex'>
                <h2>上传自己的联系方式</h2>
                <div>
                    {
                        this.state.communications.map(i => {
                            return (
                                <div
                                    className={this.state.activeId === i.id ? 'cmm-cell cmm-cell-check' : 'cmm-cell'}
                                    id={i.id}
                                    onClick={this.clickWhich.bind(this, i.id)}
                                >
                                    <img src={`${baseUrl}/${i.wx_png}`}
                                         alt=''
                                         width='60px'
                                         height='60px'
                                         onClick={
                                             ()=>{
                                             const WeixinJSBridge = window.WeixinJSBridge;
                                             WeixinJSBridge.invoke("imagePreview",{
                                                 "urls":[`${baseUrl}/${i.wx_png}`],
                                                 "current":`${baseUrl}/${i.wx_png}`
                                             });
                                         }
                                         }
                                    />
                                    联系方式:{i.phone}
                                    {this.state.activeId === i.id ? <Icon type="check"/> : null}
                                </div>
                            )
                        })
                    }
                </div>
                <div className='fix-footer'>

                    <div className='discription'>
                        非强制上传联系方式，但上传有利于双方及时联系！
                    </div>
                    <Button
                        onClick={this.showCommunicationsUpload.bind(this)}
                    >
                        添加新联系方式
                    </Button>
                    <Button
                        onClick={this.update_worker_communication.bind(this)}
                    >
                        确认上传
                    </Button>
                </div>

                <Modal
                    visible={this.state.visibleModal}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    style={{textAlign: 'center', top: 20}}

                >
                    {this.state.contentModal}
                </Modal>
            </div>

        );
    }
}


export default WorkerCmmIndex;