import React from 'react'
import './index.less'
import {Checkbox, DatePicker, Form, Icon, Input, InputNumber, message, Modal, notification, Select, Upload} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../../config";
import moment from 'moment'
import {Link} from "react-router-dom";

const {TextArea} = Input;
const FormItem = Form.Item;
const {Option} = Select;

function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

class UploadQuestionForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            work_tpl: parseInt(this.props.match.params.tpl),
            needMoney: parseInt(this.props.match.params.tpl) === 1 || parseInt(this.props.match.params.tpl) === 4 || parseInt(this.props.match.params.tpl) === 0,
            id_senting_work: '',
            communicationsId: 0,
            communications: [{
                "id": 9,
                "name": "99",
                "phone": "188888",
                "wx_png": null,
                "qq": "100",
                "wx_id": "",
                "locate": "odg"
            }],
            // 文件
            previewVisible: false,
            previewImage: '',
            previewVisibleWechat: false,
            previewImageWechat: '',
            fileList: [],
            wechat: [],
            // 多选时间组件
            valueStopAt: 1,
            valueStopAtUnix: 1,
            visibleMoreInfo: false,
            // 防止重复点击
            isClick: true,
        }
    }

    componentDidMount() {
        this.getLastCommunications();
        this.getSentingWorkID();
        this.getUserInfo();
        console.log(this.state);
        document.title = `发布${this.type_convert(this.state.work_tpl)}任务`
        message.success('已自动加载所有上次填写记录');
    }

    type_convert = (type) => {
        switch (type) {
            case 1:
                return "代办类";
            case 2:
                return "转发类";
            case 3:
                return "问卷类";
            case 4:
                return "兼职类";
            case 0:
                return "其他";
            default:
                return "全部";
        }
    };
    handleVisibleMoreInfo = () => {
        const preV = this.state.visibleMoreInfo;
        this.setState({visibleMoreInfo: !preV});
    };
    // 文件上传
    handleCancel = () => this.setState({previewVisible: false});
    handleCancelWechat = () => this.setState({previewVisibleWechat: false});

    handlePreview = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
        });
    };

    handleChange = ({fileList}) => this.setState({fileList});
    // 微信二维码
    handleChangeWechat = ({fileList}) => {
        this.setState({
            wechat: fileList,
        });
        if (fileList[0] !== undefined && fileList[0].response !== undefined) {
            this.setState({
                communicationsId: fileList[0].response.Communication.id,
            })
        }
        console.log('state', this.state);
        console.log(fileList);
    };
    handleRemoveWechat = () => {
        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    'token': localStorage.customer_token,
                    // 'id_communication': this.state.communicationsId,
                    'phone': localStorage.phone,
                    'method': 'add',
                }
            })
            .then((r) => {
                console.log('r', r);
                this.setState({
                    communicationsId: r.data.Communication.id,
                    phoneValidate: r.data.Communication.phone,
                })
            });
        return true
    };
    handlePreviewWechat = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }
        this.setState({
            previewImageWechat: file.url || file.preview,
            previewVisibleWechat: true,
        });
        console.log('预览', this.state)
    };
    // 时间组件
    onChangeStopAt = e => {
        console.log('radio checked', e);
        // console.log('radio checked', e.target.value);
        this.setState({
            valueStopAt: e,
        });
    };
    handleAfterChange = e => {
        console.log('radio checked', e.unix());
        this.setState({
            valueStopAtUnix: e.unix(),
        });
    };

    getUserInfo = () => {
        axios.get(
            baseUrl + '/api/get_user_money',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((Response) => {
                console.log(Response);
                this.setState({
                    avatarUrl: Response.data.avatarUrl,
                    nickName: Response.data.nickName,
                    coin: Response.data.coin,
                    money: Response.data.money,
                });
                console.table('res', this.state);
            })
            .catch((err) => {
                console.log(err);
            })
    };
    getSentingWorkID = () => {
        // 获取号
        axios.get(
            baseUrl + '/api/get_senting_work_id',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((Response) => {
                console.log('返回数据', Response.data);
                this.setState({
                    id_senting_work: Response.data.id,
                })
            })

    };
    getLastCommunications = () => {
        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    token: localStorage.customer_token, //用户token
                    method: 'get',
                }
            })
            .then((Response) => {
                if (Response.data.communications) {
                    console.log('getLastCommunications返回数据', Response.data);
                    let wechatLast = [];
                    if (Response.data.communications[0].wx_png !== undefined) {
                        wechatLast = [{
                            lastModified: 1545032923561,
                            lastModifiedDate: '',
                            name: "github.jpg",
                            originFileObj: '',
                            percent: 100,
                            size: 15437,
                            status: "done",
                            uid: "rc-upload-1564397617966-3",
                            response: {
                                success: "增添成功",
                                Communication:
                                    {
                                        id: Response.data.communications[0].id
                                    }
                            },
                            thumbUrl: `${baseUrl}/${Response.data.communications[0].wx_png}`,
                            type: "image/jpeg",
                        }];
                        message.success('已自动加载联系方式，若有误请重新填写');
                    }
                    this.setState({
                        communications: Response.data.communications,
                        wechat: wechatLast,
                        previewImageWechat: `${baseUrl}/${Response.data.communications[0].wx_png}`,
                        communicationsId: Response.data.communications[0].id,
                        phoneValidate: Response.data.communications[0].phone,

                    });
                    localStorage.phone = Response.data.communications[0].phone;
                    console.log(this.state)
                } else {
                    // alert(Response.data);
                }

            })
    };
    updateCmm = (p) => {
        console.log('在updateCmm', p);
        console.log('手机号', p);
        console.log(' this.state', this.state);
        axios.get(
            baseUrl + '/api/curd_communication',
            {
                params: {
                    'token': localStorage.customer_token,
                    'phone': p,
                    'id_communication': this.state.communicationsId,
                    'method': 'update',
                }
            }).then(r => {
            this.setState({
                communicationsId: r.data.Communication.id,
                phoneValidate: r.data.Communication.phone,
            })
        })
    };
    convertTimeStop = (v) => {
        if (v === 3) {
            // 自定义选项
            return this.state.valueStopAtUnix
        }
        if (v === '一周内') {
            return moment().add(7, 'day').unix()
        }
        if (v === '半小时内') {
            return moment().add(30, 'minute').unix()
        }
        if (v === '一小时内') {
            return moment().add(60, 'minute').unix()
        }
        if (v === '两小时内') {
            return moment().add(2, 'hour').unix()
        }
        if (v === '今天内') {
            return moment().endOf('day').unix()
        }
        return v
    };
    handleSubmit = () => {
        const isClick = this.state.isClick;
        console.log('点了！！！！');
        if (isClick) {
            // 获取所有的值
            let allVals = this.props.form.getFieldsValue();
            console.log(allVals);
            this.props.form.validateFields((err, values) => {
                if (!err) {
                    this.setState({isClick: false});
                    console.log('表单值', values);
                    // 发送
                    // 只上传了电话
                    if (values.wx_png === undefined) {
                        this.handleRemoveWechat()
                    }
                    // 填的手机号变了，更新
                    console.log('state', this.state);
                    console.log('电话检测', this.state.communications[0].phone, values.phone, this.state.phoneValidate);
                    if (this.state.communications[0].phone !== undefined && values.phone !== this.state.phoneValidate) {
                        this.updateCmm(values.phone)
                    }
                    axios.get(
                        baseUrl + '/api/sent_work',
                        {
                            params: {
                                'work_name': values.work_name,    //*
                                'work_tpl': values.work_tpl,  //任务类型*
                                'work_money': values.work_money,  //每个工作者的任务报酬*
                                'work_person_number': values.work_person_number,    //工作者人数*
                                'work_time': moment().unix(),   //发布时间*
                                'work_content': values.work_content === undefined ? localStorage.work_content : values.work_content,    //工作描述*
                                'stop_at': this.convertTimeStop(values.stop_at), //报名截止时间*
                                'token': localStorage.customer_token,//*
                                'id_senting_work': this.state.id_senting_work,   //get_senting_work_id返回的id*
                                'work_url': values.work_url,    //任务链接
                                'file_num': this.state.fileList.length,  //已上传的图片数
                                // 'begin_work_time': values.begin_work_time.unix(),  //工作开始时间*
                                // 'stop_work_time': values.stop_work_time.unix(),   //工作结束时间*
                                'pay_coin': 1,
                                'id_communication': this.state.communicationsId,
                                // 'sucking_person':(values.work_tpl===1||values.work_tpl===4||values.work_tpl===0)?99999:0,
                            }
                        })
                        .then((Response) => {
                            console.log(Response.data);
                            const d = Response.data;
                            if (d.success) {

                                notification.open({
                                    message: d.success,
                                    description: '正在自动跳转',
                                    placement: 'bottomLeft',
                                    icon: <Icon type="smile" style={{color: '#108ee9'}}/>,
                                });
                                setTimeout(() => {
                                    // 刷新缓存
                                    localStorage.removeItem('works_by_me');
                                    document.title = "小蓝兼职，发现更多好兼职～";
                                    document.location = `#/about/index/TaskPosted`
                                }, 1000)
                            } else {
                                message.error(d.sorry || d.error);
                                this.setState({isClick: true});
                            }
                        })
                }
            });
        }

    };
    validateTrue = (rule, value, callback) => {
        console.log(value);
        if (value !== true) {
            callback('必须同意免责声明！');
        }
        callback();
    };

    render() {
        const {getFieldDecorator, getFieldValue} = this.props.form;
        const {previewVisible, previewImage, previewVisibleWechat, previewImageWechat, fileList, wechat, needMoney} = this.state;
        const uploadButton = (
            <div>
                <Icon type="plus"/>
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        const radioStyle = {
            // display: 'block',
            // height: '30px',
            // lineHeight: '30px',
        };
        return (
            <div className='upload-work-form'>
                <Form>
                    <FormItem label='任务类型' style={{display: 'none'}}>
                        {getFieldDecorator('work_tpl', {
                            initialValue: this.state.work_tpl,
                        })(
                            <Select>
                                <Option value="1">问卷类</Option>
                                <Option value="2">转发、集赞类</Option>
                                <Option value="3">代办类</Option>
                                <Option value="4">兼职类</Option>
                                <Option value="0">其他</Option>
                            </Select>
                        )
                        }
                    </FormItem>
                    <div className=''>
                        {/*<div className='left-font'>*/}
                        {/*    基本情况*/}
                        {/*</div>*/}
                        <FormItem label='任务名'>
                            {getFieldDecorator('work_name', {
                                initialValue: localStorage.work_name,
                                rules: [{
                                    required: true,
                                    message: '不能为空'
                                }]
                            })(
                                <div>
                                    <TextArea
                                        value={localStorage.work_name}
                                        placeholder={this.state.work_tpl === 1 ? '如:代取申通快递' : this.state.work_tpl === 2 ? '如:转发图片到朋友圈不分组' : this.state.work_tpl === 3 ? '如:点击链接填写问卷' : this.state.work_tpl === 4 ? '如:上午10点酒店兼职' : '任务描述'}
                                    />
                                    <span
                                        style={{float: 'right'}}>{localStorage.work_name !== undefined ? localStorage.work_name.length : null}/40</span>
                                </div>
                            )
                            }
                        </FormItem>

                        <FormItem label='需要人数'>
                            {getFieldDecorator('work_person_number', {
                                initialValue: localStorage.work_person_number,
                                rules: [
                                    {
                                        pattern: /^[1-9]\d*$/, message: '请填正整数'
                                    },
                                    {
                                        required: true,
                                        message: '人数不能为空',
                                    }]
                            })(<InputNumber placeholder='征集人数'/>)
                            }
                        </FormItem>
                        <FormItem label='报酬'>
                            {getFieldDecorator('work_money', {
                                initialValue: localStorage.work_money,
                                rules: [
                                    {
                                        pattern: /^[1-9]\d*$/, message: '请填正整数'
                                    },
                                    {
                                        required: true,
                                        message: '必填',
                                    }],
                            })(
                                <div>
                                    <Input placeholder='默认为1' value={localStorage.work_money} className='pay'
                                           width='40%'/>
                                    <span>{needMoney ? '元' : `硬币(=${localStorage.work_money / 100}元)`}/人</span>
                                </div>
                            )
                            }
                        </FormItem>
                        <FormItem label='联系方式(微信或手机号)'>
                            {getFieldDecorator('phone', {
                                initialValue: '' || this.state.phoneValidate,
                                rules: [{
                                    required: true,
                                    message: '不能为空'
                                }]
                            })(<Input placeholder='18888888888'/>)
                            }
                            {/*<span>是否可见</span>*/}
                            {/*<input type="checkbox" className="al-toggle-button"/>*/}
                        </FormItem>
                        <FormItem label='微信二维码'>
                            {getFieldDecorator('wx_png', {
                                // rules: [{
                                //     required: true,
                                //     message: '不能为空'
                                // }]
                            })(<div>
                                <Upload
                                    className="wx-uploader"
                                    name='wx_png'
                                    action={`${baseUrl}/api/curd_communication?token=${localStorage.customer_token}&phone=${getFieldValue('phone')}`}
                                    listType="picture-card"
                                    fileList={wechat}
                                    onPreview={this.handlePreviewWechat}
                                    onChange={this.handleChangeWechat}
                                    onRemove={this.handleRemoveWechat}
                                    data={{
                                        token: localStorage.customer_token,
                                        method: 'add',     //给自己增加一组联系方式
                                        phone: getFieldValue('phone'),
                                    }}
                                >
                                    {wechat.length >= 1 ? null : uploadButton}
                                </Upload>
                                <p>请先填写好联系方式再点击上传二维码</p>
                                <Modal visible={previewVisibleWechat} footer={null} onCancel={this.handleCancelWechat}
                                       style={{width: '100%'}}>
                                    <img alt="example" style={{width: '100%'}} src={previewImageWechat}/>
                                </Modal>
                            </div>)
                            }

                        </FormItem>
                        <FormItem label='截止时间' className='stop-at-choser'>
                            {getFieldDecorator('stop_at', {
                                rules: [{
                                    required: true,
                                    message: '必填',
                                }]
                            })(
                                <Select onChange={this.onChangeStopAt} defaultValue="今天内">
                                    <Option style={radioStyle} value='半小时内'>
                                        半小时内
                                    </Option>
                                    <Option style={radioStyle} value='一小时内'>
                                        一小时内
                                    </Option>
                                    <Option style={radioStyle} value='两小时内'>
                                        两小时内
                                    </Option>
                                    <Option style={radioStyle} value='今天内'>
                                        今天内
                                    </Option>
                                    <Option style={radioStyle} value='一周内'>
                                        一周内
                                    </Option>
                                    <Option style={radioStyle} value={3}>
                                        其他时间
                                        <DatePicker
                                            showTime
                                            format='MM-DD HH:mm'
                                            onChange={this.handleAfterChange}
                                        />
                                    </Option>
                                </Select>
                            )}
                        </FormItem>
                        <FormItem>
                            {getFieldDecorator('checkForLaw', {
                                initialValue: true,
                                rules: [
                                    {
                                        validator: this.validateTrue,
                                    },]
                            })(
                                <Checkbox defaultChecked>本人承担所发布任务的任何法律责任，与平台无关</Checkbox>
                            )}
                        </FormItem>

                    </div>
                    <div className='more-info'>
                        <div className='left-font' onClick={() => this.handleVisibleMoreInfo()}>
                            高级功能
                            <span className='right-font'>
                                {this.state.visibleMoreInfo === false
                                    ? <Icon type="down"/>
                                    : <Icon type="up"/>
                                }
                            </span>
                        </div>
                        {this.state.visibleMoreInfo === true ?
                            <div>

                                <div>
                                    <FormItem label='任务相关链接(选填)'>
                                        {getFieldDecorator('work_url',
                                            {
                                                initialValue: localStorage.work_url,
                                            },
                                        )(<TextArea placeholder='任务相关链接'/>)
                                        }
                                    </FormItem>
                                    <FormItem label='任务相关图片说明(选填)'>

                                        <div>
                                            <Upload
                                                action={baseUrl + '/api/upload_work_file'}
                                                listType="picture-card"
                                                fileList={fileList}
                                                onPreview={this.handlePreview}
                                                onChange={this.handleChange}
                                                data={{
                                                    token: localStorage.customer_token,
                                                    id_work: this.state.id_senting_work
                                                }}
                                            >
                                                {fileList.length >= 1 ? null : uploadButton}
                                            </Upload>
                                            <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                                                <img alt="example" style={{width: '100%'}} src={previewImage}/>
                                            </Modal>
                                        </div>
                                    </FormItem>
                                    <FormItem className='big-big-more'>
                                        {getFieldDecorator('work_content',
                                            {
                                                // initialValue: localStorage.work_content,
                                            },
                                        )(
                                            <div>
                                                <h3>补充说明（选填）</h3>
                                                <TextArea value={localStorage.work_content} rows={10}/>
                                            </div>
                                        )
                                        }
                                    </FormItem>
                                </div>
                            </div>
                            : null}
                    </div>
                    <FormItem>
                        <div className='submit-footer'>
                            {!needMoney
                                ? <div className='coin-count'>
                                    <div className='coin-count-cell'>
                                        需要：{getFieldValue('work_person_number') * getFieldValue('work_money')}硬币({getFieldValue('work_person_number') * getFieldValue('work_money') / 100}人民币)
                                    </div>
                                    <div className='coin-count-cell'>
                                        我的硬币：{this.state.money}
                                        {
                                            getFieldValue('work_person_number') * getFieldValue('work_money') > this.state.money
                                                ?
                                                <span style={{color: 'red'}}>硬币不足，<Link to='/pay'>充值</Link></span>
                                                : null
                                        }
                                    </div>
                                </div>
                                : null}
                            <div className='submit-footer-cell' onClick={this.handleSubmit}>
                                {this.state.isClick ? '发布' : '发布中...'}
                            </div>
                        </div>
                    </FormItem>
                </Form>
            </div>
        )
    }
}

export const UploadQuestion = Form.create({
    // onFieldsChange(props, changedFields) {
    //     props.onChange(changedFields);
    // },
    onValuesChange(_, v) {
        const k = Object.keys(v)[0];
        console.log(v, v[k], k);
        localStorage[k] = v[k];
    },
})(UploadQuestionForm);
